import React from "react";
import PizzaBlock from "./PizzaBlock";
import {useDispatch, useSelector} from "react-redux";
import PizzaLoadingBlock from "./PizzaLoadingBlock";
import {fetchPizzas} from "../redux/action/pizzas";
import Categories from "./Categories";
import Sort from "./Sort";
import {setSortBy} from "../redux/action/filter";

function Home() {

    const {items, isLoaded} = useSelector(({pizzas}) => pizzas)
    const {category, sortBy} = useSelector(({filter}) => filter)
    const cartItems = useSelector(({cart}) => cart.items)

    const dispatch = useDispatch();

    const onSelectSortType = (type) => {
        dispatch(setSortBy(type))
    }

    const handleAddPizzaToCart = (obj) => {
        dispatch({
            type: 'ADD_PIZZA_CART',
            payload: obj,
        })
    }

    React.useEffect(() => {
        dispatch(fetchPizzas(sortBy, category))
    }, [category, sortBy])

    return (
        <div className="container">
            <div className="content__top">
                <Categories items={["–í—Å–µ", "–ú—è—Å–Ω—ã–µ", "–í–µ–≥–µ—Ç–µ—Ä–∏–∞–Ω—Å–∫–∞—è", "–ì—Ä–∏–ª—å", "–û—Å—Ç—Ä—ã–µ", "–ó–∞–∫—Ä—ã—Ç—ã–µ"]}
                            activeCategory={category}/>
                <Sort items={[
                    {name: "–ø–æ–ø—É–ª—è—Ä–Ω–æ—Å—Ç–∏", type: "popular"},
                    {name: "—Ü–µ–Ω–µ", type: 'price'},
                    {name: "–∞–ª—Ñ–∞–≤–∏—Ç—É", type: 'name'}
                ]}
                      activeSortType={sortBy}
                      onClickSortType={onSelectSortType}/>
            </div>
            <h2 className="content__title">–í—Å–µ –ø–∏—Ü—Ü—ã</h2>
            <div className="content__items">
                {isLoaded
                    ? items.map((obj) =>
                        (<PizzaBlock onClickAddPizza={handleAddPizzaToCart}
                                     addedCount={cartItems[obj.id] && cartItems[obj.id].items.length}
                                     key={obj.id}
                                     isLoading={true}
                                     {...obj}
                            />
                        ))
                    : Array(12).fill(<PizzaLoadingBlock/>)
                }
            </div>
        </div>
    )
}

export default Home;

